import React, { useState } from 'react';
import bridge from '@vkontakte/vk-bridge';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import List from '@vkontakte/vkui/dist/components/List/List';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Input from '@vkontakte/vkui/dist/components/Input/Input';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import PanelHeaderBack from '@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Header from '@vkontakte/vkui/dist/components/Header/Header';


const Settings = (props) => {

	const [lengthSearch, setStateLengthSearch] = useState(0)
	const [checkClick, setStateCheckClick] = useState(false)

	const [descriptionAdress0, setStateDescriptionAdress0] = useState('')
	const [nameAdress0, setStateNameAdress0] = useState()
	const [position0, setStatePosition0] = useState()

	const [descriptionAdress1, setStateDescriptionAdress1] = useState('')
	const [nameAdress1, setStateNameAdress1] = useState()
	const [position1, setStatePosition1] = useState()

	
	const [descriptionAdress2, setStateDescriptionAdress2] = useState()
	const [nameAdress2, setStateNameAdress2] = useState()
	const [position2, setStatePosition2] = useState()

	const [description0, setStateDescription0] = useState('Предпочтительнее выбрать то место, где вы проживаете.')
	const [description1, setStateDescription1] = useState('Внимание, на устройстве должны быть включены службы геолокации.')
	const [description2, setStateDescription2] = useState()

	const [chooseGeo0, setStateChooseGeo0] = useState()
	const [chooseGeo1, setStateChooseGeo1] = useState()

	const [chooseDescription, setStateChooseDescription] = useState()
	const [chooseName, setStateChooseName] = useState()

	
	
	

	const searchChange = () =>  {
		setStateLengthSearch(document.getElementById('search').value.length)
		const key = `c16841d0-5344-4254-a4fd-32e30927fa2a`;
		const api = `https://geocode-maps.yandex.ru/1.x/?format=json&geocode=${document.getElementById('search').value}&apikey=${key}`;

		fetch(api)
			.then(res => res.json())
			.then(data => {
				setStateCheckClick(false)
				setStateNameAdress0((data.response.GeoObjectCollection.featureMember[0]).GeoObject.name)
				setStatePosition0((data.response.GeoObjectCollection.featureMember[0]).GeoObject.Point.pos.split(' ').reverse())
				setStateDescriptionAdress0((data.response.GeoObjectCollection.featureMember[0]).GeoObject.description)
				setStateNameAdress1((data.response.GeoObjectCollection.featureMember[1]).GeoObject.name)
				setStatePosition1((data.response.GeoObjectCollection.featureMember[1]).GeoObject.Point.pos.split(' ', 2).reverse())
				setStateDescriptionAdress1((data.response.GeoObjectCollection.featureMember[1]).GeoObject.description)
				setStateNameAdress2((data.response.GeoObjectCollection.featureMember[2]).GeoObject.name)
				setStateDescriptionAdress2((data.response.GeoObjectCollection.featureMember[2]).GeoObject.description)
				setStatePosition2((data.response.GeoObjectCollection.featureMember[2]).GeoObject.Point.pos.split(' ', 2).reverse())
			})

			.catch(e => {
			})	
	}

	const getPosition0 = () => {
		
		if (descriptionAdress0 === undefined || descriptionAdress0 === '') {
			setStateChooseGeo0()
			setStateDescription0('Ошибка при получении данные, воспользуйтесь GPS.')
			document.getElementById('search').value = ''
			setStateLengthSearch(0)
		}
		else {
			setStateDescription0('Расстояние до городов будет рассчитываться относительно этого места.')
			setStateDescription1('Внимание, на устройстве должны быть включены службы геолокации.')
			setStateChooseGeo0(`${descriptionAdress0}, ${nameAdress0}.`)
			setStateChooseGeo1()
			document.getElementById('search').value = ''
			setStateChooseDescription(descriptionAdress0)
			setStateChooseName(nameAdress0)
			setStateLengthSearch(0)
			setStateNameAdress0()
			setStateDescriptionAdress0()
			setStateNameAdress1()
			setStateDescriptionAdress1()
			setStateNameAdress2()
			setStateDescriptionAdress2()
			props.savePositionGlobal(`${position0}`, `${descriptionAdress0}, ${nameAdress0}`)
		}

	}
	const getPosition1 = () => {
		
		if (descriptionAdress1 === undefined || descriptionAdress1 === '') {
			setStateChooseGeo0()
			setStateDescription0('Ошибка при получении данные, воспользуйтесь GPS.')
			document.getElementById('search').value = ''
			setStateLengthSearch(0)
		}
		else {
			setStateDescription0('Расстояние до городов будет рассчитываться относительно этого места.')
			setStateDescription1('Внимание, на устройстве должны быть включены службы геолокации.')
			setStateChooseGeo0(`${descriptionAdress0}, ${nameAdress0}.`)
			setStateChooseGeo1()
			document.getElementById('search').value = ''
			setStateChooseDescription(descriptionAdress1)
			setStateChooseName(nameAdress1)
			setStateLengthSearch(0)
			setStateNameAdress0()
			setStateDescriptionAdress0()
			setStateNameAdress1()
			setStateDescriptionAdress1()
			setStateNameAdress2()
			setStateDescriptionAdress2()
			props.savePositionGlobal(`${position1}`, `${descriptionAdress1}, ${nameAdress1}`)
		}

	}
	const getPosition2 = () => {
		
		if (descriptionAdress2 === undefined || descriptionAdress2 === '') {
			setStateDescription0('Ошибка при получении данные, воспользуйтесь GPS.')
			setStateChooseGeo0()
			document.getElementById('search').value = ''
			setStateLengthSearch(0)
		}
		else {
			setStateDescription0('Расстояние до городов будет рассчитываться относительно этого места.')
			setStateDescription1('Внимание, на устройстве должны быть включены службы геолокации.')
			setStateChooseGeo0(`${descriptionAdress0}, ${nameAdress0}.`)
			setStateChooseGeo1()
			document.getElementById('search').value = ''
			setStateChooseDescription(descriptionAdress2)
			setStateChooseName(nameAdress2)
			setStateLengthSearch(0)
			setStateNameAdress0()
			setStateDescriptionAdress0()
			setStateNameAdress1()
			setStateDescriptionAdress1()
			setStateNameAdress2()
			setStateDescriptionAdress2()
			props.savePositionGlobal(`${position2}`, `${descriptionAdress2}, ${nameAdress2}`)
		}
	}

	const getPositionWithGps = () => {

		bridge.send("VKWebAppGetGeodata")
			.then(data => {

				if (data.lat === undefined) {
					setStateDescription1('Данные не получены. Попробуйте включить службы геопозиции в самом устройстве.')
				} else {
					setStateDescription0('Предпочтительнее выбрать то место, где вы проживаете.')
					setStateDescription1('Расстояние до городов будет рассчитываться относительно этой точки.')
					setStateChooseGeo0()
					setStateChooseGeo1(`Широта - ${(data.lat).toFixed(5)}, долгота - ${(data.long).toFixed(5)}`)
					props.savePositionGlobal(`${data.lat},${data.long}`, `Широта - ${data.lat}. Долгота - ${data.long}.`)
				}

			})
			.catch(error => {
				setStateDescription1('Данные не получены. Дайте разрешение на использование геоданных или выберите город/населенный пункт через поиск выше.')
			})

	}

	return (

	<Panel id={props.id} >
            <PanelHeader left={<PanelHeaderBack onClick={() => window.history.back()} />}>
			Настройки
        </PanelHeader>
		<div className='stageSucces'>
<div style={{position: 'relative', marginTop: '15%'}}>
				<Group header={
					<Header>Выбрать город/населенный пункт</Header>}>
				<Div><Input placeholder='Поиск' id="search" onChange={() => searchChange()} /></Div>
						{lengthSearch > 0 ? (
							<List >
								<Cell multiline={true}
									description={descriptionAdress0}
									onClick={() => getPosition0()}>
									{nameAdress0}

								</Cell>
								<Cell multiline={true}
									description={descriptionAdress1}
									onClick={() => getPosition1()}>
									{nameAdress1}
								</Cell>
								<Cell multiline={true}
									description={descriptionAdress2}
									onClick={() => getPosition2()}>
									{nameAdress2}
								</Cell>
							</List>
						) : (
								<Cell multiline={true}
							description={description0}>
							{chooseGeo0}
								</Cell>
							)}

				</Group>
			<Group header={
				<Header>Использовать GPS</Header>}>
				<Div>
					<Button onClick={() => getPositionWithGps()} size='xl' mode='secondary'>Получить геоданные</Button></Div>
				<Cell description={description1} multiline={true}>
					{chooseGeo1}
					</Cell>
			</Group>
			<Group description={`Активирована тема - ${props.scheme}`} header={
				<Header>Изменить тему</Header>}>
				<Div>
					<Button onClick={() => props.camelCase(props.scheme, true)} size='xl' mode='secondary'>Сменить тему</Button>
				</Div>

			</Group>
				</div>
			</div>
    </Panel>
	)};

export default Settings;





